  border-radius: 3px;
  font-size: 0.8em;
  background-color: rgba(0, 0, 0, 0.06); }

pre code {
  padding: 0;
  font-size: 0.8em;
  background-color: inherit; }

h1, h2, h3, h4, h5, h6,
ul, ol, dl,
blockquote, p, address,
hr,
table,
fieldset, figure,
pre {
  margin-top: 0;
  margin-bottom: 0; }

